.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#profilePhoto {
  max-width: 444px;
  max-height: 444px;
  float: left;
}

@media only screen and (max-width: 575px)  {
  #profilePhoto {
    max-width: 558px;
    max-height: 558px;
  }
}

#info div {
  float: left;
}

#info, .imgDiv {
  padding-left: 0px;
}

#info {
  background: #fff;
  margin-top: 200px;
}

.subTitle {
  font-size: 1.4rem;
}

body {
  background: #e7e7e7;
  font-family: 'Quicksand', sans-serif;
}

.shadow {
  -webkit-box-shadow: 0px 0px 12px -1px rgba(79,79,79,1);
  -moz-box-shadow: 0px 0px 12px -1px rgba(79,79,79,1);
  box-shadow: 0px 0px 12px -1px rgba(79,79,79,1);
}

.maxWidth {
  min-height: 375px;
  min-width: 100%;
  background-color: #355c7d;
  position: absolute;
  color: #fff;
}

.colorized {
  color: #355c7d;
}

#welcome span {
  background: #355c7d;
  color: #fff;
  padding: 5px;
}

#welcome {
  margin-top: 30px;
}

#info .col-sm-7 * {
  text-align: left;
}

.headingLinks {
  height: 50px;
  background: #355c7d;
  color: #fff;
  width: 106%;
  position: absolute;
  bottom: -51px;
  left: -24px;
}

.headingLinks .col-sm-2 {
  text-align: center !important;
}

.headingLinks .col-sm-2 svg{
  font-size: 1.8rem;
  margin-top: 10px;
}

a {
  color: #000;
}

.headingLinks a{
  color: #fff;
}

.top60 {
  margin-top: 60px;
  text-align: left;
}

.top60c {
  margin-top: 60px;
  text-align: center;
}

.left {
  float: left;
}

.right {
  float: right;
}

.skillsDiv {
  background-color: #fff;
  margin-top: 40px;
  padding: 15px;
}

.react-sweet-progress-line-inner, .react-sweet-progress-line-inner-status-active {
  background-color: rgb(53, 92, 125) !important;
}

.top15 {
  margin-top: 10px;
}

button svg {
  font-size: 1.4rem;
  margin-left: 5px;
}

#skillzResume {
  text-align: left;
}

.skillzTitle p {
  margin: 0px !important;
  margin-top: 10px !important;
}

.aws-btn {
  --button-primary-color: #316a9b !important;
  --button-primary-color-dark: #1e5381 !important;
  --button-primary-color-hover: #1c476c !important;
  --button-primary-color-active: #143551 !important;
}

@media only screen and (max-width: 1199px)  {
  #profilePhoto {
    max-width: 393px;
    max-height: 393px;
  }

  .headingLinks {
    left: -394px;
    width: 174%;
  }

  .top60cSpecial {
    margin-top: 100px;
  }
}

@media only screen and (max-width: 991.5px)  {
  #profilePhoto {
    max-width: 720px;
    max-height: 720px;
  }

  .headingLinks {
    left: 1px;
    width: 102%;
  }
}

@media only screen and (max-width: 767.5px)  {
  #profilePhoto {
    max-width: 540px;
    max-height: 540px;
  }

  .headingLinks {
    left: 0px;
    width: 103%;
  }
}

@media only screen and (max-width: 575.5px)  {
  #profilePhoto {
    max-width: 106.5%;
    max-height: none;
  }

  .headingLinks {
    left: 0px;
    width: 103%;
  }

  .headingLinks .col-sm-2 {
    width: 20%;
  }
}

.firstEducation {
  margin-top: 40px;
}

.leftEducation {
  max-height: 140px;
  background-color: #fff;
  padding-top: 15px;
  padding-left: 15px;
  padding-right: 15px;
  max-width: 535px;
  margin-right: 20px;
}

.rightEducation {
  margin-top: 100px;
  max-height: 140px;
  background-color: #fff;
  padding-top: 15px;
  padding-left: 15px;
  padding-right: 15px;
  max-width: 535px;
  margin-left: 20px;
}

.circle-left {
  height: 15px;
  width: 15px;
  background-color: #355c7d;
  border-radius: 50%;
  position: absolute;
  left: -27px;
  z-index: -1;
  top: 19px;
}

.circle-right {
  height: 15px;
  width: 15px;
  background-color: #355c7d;
  border-radius: 50%;
  position: absolute;
  right: -27px;
  z-index: -1;
  top: 19px;
}

.triangle-left {
  width: 0;
  height: 0;
  border-top: 12px solid transparent;
  border-right: 19px solid #fff;
  border-bottom: 12px solid transparent;
  position: absolute;
  left: -19px;
}

.triangle-right {
  width: 0;
  height: 0;
  border-top: 12px solid transparent;
  border-left: 19px solid #fff;
  border-bottom: 12px solid transparent;
  position: absolute;
  right: -19px;
}

.lastEducation {
  margin-bottom: 50px;
}

@media only screen and (max-width: 1199px)  {
  .rightEducation, .leftEducation {
    max-width: 445px;
  }
}

@media only screen and (max-width: 991.5px)  {
  .rightEducation, .leftEducation {
    max-width: 325px;
  }

  .doubleLine {
    max-height: 155px;
  }
}

@media only screen and (max-width: 767.5px)  {
  .rightEducation, .leftEducation {
    max-width: 235px;
  }

  .doubleLine {
    max-height: 155px;
  }
}

@media only screen and (max-width: 575.5px)  {
  .triangle-right, .triangle-left, .circle-left, .circle-right {
    display: none;
  }

  .rightEducation, .leftEducation {
    max-width: none;
    margin-left: 0;
    margin-right: 0;
    margin-top: 20px;
  }
}